/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

*,
*::after,
*::before,
body {
  box-sizing: border-box;
  text-decoration: none;
  line-height: 1.5;
  font-family: 'Noto Sans TC', sans-serif, 'Roboto', sans-serif, -apple-system, BlinkMacSystemFont, "Segoe UI", "Microsoft JhengHei", Roboto, "Helvetica Neue", Arial, sans-serif; }

.container {
  max-width: 1326px;
  margin: 0 auto;
  padding: 0 15px; }
  @media (max-width: 992px) {
    .container {
      max-width: 1064px;
      padding: 0 36px; } }
  @media (max-width: 768px) {
    .container {
      max-width: 768px;
      padding: 0 36px; } }
  @media (max-width: 320px), (max-width: 575px) {
    .container {
      max-width: 575px;
      padding: 0 14px; } }

.d-flex {
  display: flex; }

.align-center {
  align-items: center; }

.justify-content-between {
  justify-content: space-between; }

.p-80 {
  padding: 80px 0; }
  @media (max-width: 320px), (max-width: 575px) {
    .p-80 {
      padding: 40px 0; } }

.blog-nav {
  background-color: #650300; }
  .blog-nav a {
    display: block;
    color: #FFFFFF;
    font-size: 1.25rem;
    font-weight: bold;
    line-height: 56px; }
    .blog-nav a:hover {
      font-weight: normal; }
  .blog-nav li {
    margin-right: 24px; }
  .blog-nav span {
    color: #FFFFFF;
    font-size: 29px;
    cursor: pointer; }
  @media (max-width: 576px) {
    .blog-nav {
      display: none; } }

.blog {
  padding: 96px 0 64px 0; }
  .blog h2 {
    margin-bottom: 32px;
    border-left: 8px solid #9F1213;
    padding-left: 16px;
    font-size: 3rem;
    font-weight: bold; }
  .blog img {
    max-width: 636px;
    margin-right: 1%; }
  .blog .article-preview {
    width: 49%;
    flex-direction: column; }
  .blog li {
    margin-bottom: 60px; }
  .blog h3 {
    font-size: 32px;
    color: #AA0601; }
  .blog h4 {
    display: inline-block;
    margin-bottom: 16px;
    font-weight: 500; }
  .blog p {
    color: #555555;
    font-size: 1.25rem; }
  .blog a {
    display: block;
    color: #AA0601;
    font-weight: 900;
    font-size: 2.25rem;
    text-align: right; }
  @media (max-width: 992px) {
    .blog img {
      max-width: 48%;
      height: auto;
      object-fit: cover; }
    .blog .article-preview {
      width: 48%; }
    .blog p {
      font-size: 1rem; } }
  @media (max-width: 768px) {
    .blog h3 {
      font-size: 1.5rem; }
    .blog h4 {
      font-size: 1rem;
      margin-bottom: 4px; }
    .blog a {
      display: none; }
    .blog p {
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 6;
      text-overflow: ellipsis;
      overflow: hidden; } }
  @media (max-width: 576px) {
    .blog {
      padding: 40px 0; }
      .blog h2 {
        font-size: 1.5rem; }
      .blog li {
        flex-direction: column;
        height: auto;
        margin-bottom: 24px; }
      .blog p {
        display: none; }
      .blog img {
        max-width: 100%;
        max-height: 208px;
        margin-bottom: 8px; }
      .blog .article-preview {
        width: 100%; } }

.pages {
  margin-bottom: 80px;
  height: 48px; }
  .pages ul {
    justify-content: center; }
  .pages li {
    margin-right: -1px;
    line-height: 48px;
    text-align: center;
    border: 1px solid #555555; }
    .pages li a {
      color: #555555;
      padding: 0 16px; }
    .pages li:hover {
      background-color: #F2F2F2; }
  .pages .select a {
    color: #FFFFFF;
    background-color: #000000; }
  .pages .last li a,
  .pages .next li a {
    background-color: #FFFFFF;
    padding: 0 12px; }
  .pages a {
    display: block; }

.blog-article {
  padding: 24px 0 80px 0; }
  .blog-article a {
    color: #000000; }
  .blog-article .tag {
    color: #AA0601; }
  .blog-article ul {
    max-width: 856px;
    flex-direction: column;
    margin-right: 24px; }
  .blog-article li {
    margin-bottom: 40px; }
    .blog-article li img {
      display: block;
      width: 100%;
      height: auto;
      margin-bottom: 8px; }
  .blog-article h2 {
