.title {
  color: #AA0601; }

.content li {
  color: #555555; }

.tag {
  max-width: 260px;
  font-weight: bold;
  color: #AA0601; }
  .tag h2 {
    font-size: 32px; }
  .tag span {
    font-size: 38px;
    font-weight: bold;
    margin-right: 12px;
    margin-bottom: 0; }
  .tag a {
    display: inline-block;
    color: #AA0601;
    font-size: 1.5rem;
    margin-right: 24px;
    margin-bottom: 4px; }
  @media (max-width: 768px) {
