.page-last-next a {
  color: #000000; }
  .page-last-next a:hover {
    font-weight: bold; }

.page-last-next p {
  display: inline-block;
  font-size: 1rem;
  font-family: "Noto Sans TC", sans-serif;
  margin: 0 12px; }

.page-last-next span {
  font-size: 1rem;
  font-weight: bold; }
